@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;700&family=Roboto+Slab:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track-piece {
  background-color: white;
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(39, 39, 39);
}

.cart-items {
  display: flex;
}

.items {
  list-style: none;
  margin: 0 20px;
  width: 200px;
}

.ty {
  margin: auto;
  margin-top: 30vh;
  text-align: center;
}

.ty p {
  font-size: 1.4em;
}

.ty a {
  border-radius: 15px;
  color: black;
  font-size: 1.4em;
  padding: 8px 15px;
  text-decoration: none;
  transition: 0.5s;
}

.ty a:hover {
  background-color: black;
  color: white;
}

.seta {
  width: 200px;
}

.div-seta-check {
  align-items: start;
  display: flex;
  flex-direction: column;
  left: 150px;
  position: absolute;
  top: 200px;
}

.header {
  align-items: center;
  background-color: black;
  display: flex;
  justify-content: space-between;
  padding: 20px 150px;
}

.text-header-h1, .text-header-p {
  border-radius: 20px;
  color: white;
  padding: 6px 15px;
  text-decoration: none;
  transition: 0.5s;
}

.text-header-h1 {
  font-family: Roboto Slab , serif;
  font-size: 1.4em;
  font-weight: 500;
}

.text-header-p {
  font-size: 1.2em;
}

.text-header-p:hover {
  background-color: white;
  color: black;
}

.search-div {
  align-items: center;
  display: flex;
  left: 45%;
  position: absolute;
  top: 65px;
}

.imput-search {
  border: none;
  font-size: 1.2em;
  height: 30px;
  padding: 5px;
  width: 300px;
}

.button-search {
  border: none;
  color: rgb(130, 130, 130);
  cursor: pointer;
  font-size: 1.2em;
  height: 40px;
  padding: 5px;
  transition: 0.5s;
}

.button-search:hover {
  color: black;
}

.cart-items {
  margin-top: 20px;
}

.items {
  border: 1px solid rgb(112, 112, 112);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 20px 10px;
  text-align: center;
  transition: 0.2s;
  width: 240px;
}

.items p, .items img, .items button {
  margin-bottom: 10px;
}

.items img {
  margin: auto;
}

.total-value h1, .total-value p, .total-value button {
  margin-bottom: 60px;
}

.items button, .total-value button {
  border: none;
  color: rgb(130, 130, 130);
  cursor: pointer;
  font-size: 1.2em;
  height: 40px;
  padding: 5px;
  transition: 0.5s;
}

.items button:hover, .total-value button:hover {
  background-color: black;
  color: white;
}

.cart-section {
  align-items: center;
  display: flex;
  justify-content: space-around;
}

.total-value {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  text-decoration: none;
}

.total-value h1 {
  font-size: 2.8em;
}

.total-value p {
  font-size: 1.3em;
}

.empty-car {
  font-size: 2em;
  left: 40vw;
  position: absolute;
  top: 50vh;
}

.category-css {
  display: flex;
  flex-direction: column;
  height: 720px;
  margin: 20px 100px 0 10px;
  min-width: 250px;
  overflow-y: scroll;
}

.category {
  cursor: pointer;
  margin-bottom: 10px;
}

.category:hover {
  text-decoration: underline;
}

.category-css::-webkit-scrollbar {
  width: 10px;
}

.category-css::-webkit-scrollbar-track-piece {
  background-color: white;
}

.category-css::-webkit-scrollbar-thumb {
  background-color: #0000009d;
}

.home-css {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  justify-content: flex-start;
}

.items-search {
  display: flex;
  flex-wrap: wrap;
  margin: 20px auto;
  text-align: center;
}

.items-search button {
  border: none;
  color: rgb(130, 130, 130);
  cursor: pointer;
  font-size: 1.2em;
  height: 40px;
  padding: 5px;
  transition: 0.5s;
}

.items-search button:hover {
  background-color: black;
  color: white;
}

.items-list {
  border: 1px solid rgb(112, 112, 112);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 10px;
  transition: 0.2s;
  width: 240px;
}

.items-list:hover {
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.316);
}

.product-info {
  color: black;
  text-decoration: none;
}

.main-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  padding: 0 180px;
}

.form button, .form p, .form input, .form textarea {
  margin-bottom: 70px;
}

.form input {
  font-size: 0.8em;
  height: 30px;
  padding: 5px;
}

.item-info {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: 1.5em;
}

.item-info > :nth-child( even ) {
  margin-bottom: 70px;
}

.item-info > :nth-child( odd ) {
  margin-bottom: 70px;
}

.item-info img {
  width: 200px;
}

.item-info button, .form button {
  border: none;
  color: rgb(130, 130, 130);
  cursor: pointer;
  font-size: 1em;
  height: 50px;
  padding: 10px;
  transition: 0.5s;
}

.item-info button:hover, .form button:hover {
  background-color: black;
  color: white;
}

.item-describe {
  height: 600px;
}

.form {
  display: flex;
  flex-direction: column;
  font-size: 1.6em;
  height: 600px;
  text-align: center;
  width: 400px;
}

.form textarea {
  font-size: 0.9em;
  height: 100px;
  resize: none;
}

.avaliation-radio {
  display: flex;
  justify-content: center;
}

.avaliation-radio label {
  margin-left: 10px;
  margin-right: 10px;
}

